.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  border: none;
  border-radius: 50%;
}
.text1 {
  color: rgb(167, 54, 54);
  font-weight: 600;
  font-size: 3rem;
}

.text2 {
  color: rgb(146, 139, 139);
  font-weight: 800;
  font-size: 1.5rem;
}
